.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body.show-modal .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

body.show-modal .modal {
  transform: scale(1);
}

.backdrop {
  width: 100vw;
  height: 100vh;
  pointer-events: none;
  z-index: 50;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, .3);
  justify-content: center;
  align-items: center;
  transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 500px;
  background-color: #fff;
  border-radius: 4px;
  padding: 20px;
  transition: transform .2s ease-in-out;
  transform: scale(1.1);
}

